@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
}

.app {
  height: 100vh;
}

.chatbar {
  height: 10vh;
  display: flex;
}

.chatbar input {
  font-size: 1rem;
}

.text {
  flex: 85% 1;
  background-color: #f5f5f5;
  outline: none;
  border: none;
  padding: 15px;
}

.submit {
  flex: 15% 1;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: #5ea3d0;
  color: white;
  outline: none;
  border: none;
}
/*# sourceMappingURL=chatBar.css.map */
.modal {
  border: 1.5px solid #333;
  box-shadow: 5px 5px #333;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 14px;
  padding: 14px;
  text-align: center;
}

.modal h1 {
  margin-bottom: 14px;
}

.modal button {
  border: none;
  outline: none;
  font-size: 1.2rem;
  padding: 0.3rem;
  margin: 0.5rem;
  background-color: white;
  color: #3e5869;
}

.modal button:hover {
  cursor: pointer;
}
/*# sourceMappingURL=modal.css.map */
.message {
  margin-top: 20px;
  margin-right: 10px;
  margin-left: 10px;
  background-color: #5ea3d0;
  color: white;
  padding: 1rem;
  border-radius: 30px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  word-wrap: break-word;
}

.message .admin {
  margin-bottom: 0.5rem;
}

.message-user {
  background-color: #7e5f4a;
  color: white;
  margin-top: 20px;
  margin-right: 10px;
  margin-left: 10px;
  padding: 1rem;
  border-radius: 30px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  word-wrap: break-word;
}

.message .messageText:hover {
  cursor: pointer;
}
/*# sourceMappingURL=chatMessage.css.map */
.chat-messages {
  height: 80vh;
  overflow: scroll;
  padding: 1.5rem;
}
/*# sourceMappingURL=chatShell.css.map */
.title {
  display: flex;
  flex-direction: column;
  font-size: 1.5rem;
  justify-content: center;
  align-items: center;
  height: 10vh;
  background-color: #5ea3d0;
  color: white;
}

.title .filter {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 0.5rem;
}

.logout {
  background-color: white;
  color: #5ea3d0;
  font-size: 1rem;
  margin-top: 0.5rem;
  padding: 0.5rem;
  cursor: pointer;
}
/*# sourceMappingURL=header.css.map */
.container {
  max-width: 600px;
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;
  text-align: center;
}

.container h1 {
  margin-bottom: 1rem;
}

.container p {
  margin-bottom: 3rem;
}

.container label {
  display: block;
  text-align: left;
}

.container input {
  border: none;
  border-bottom: 1px solid #ccc;
  width: 100%;
  display: block;
  padding: 1rem;
  font-size: 1rem;
  margin-bottom: 1rem;
  outline: none;
}

.container input:focus {
  border-bottom: 1px solid #3e5869;
}

.container .submit {
  width: 25%;
  outline: none;
  border: none;
  cursor: pointer;
  margin: auto;
  margin-top: 2rem;
}
/*# sourceMappingURL=login.css.map */
